* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
    color: #fffefa;
    font-family: "Lato", 'Arial', sans-serif;
    font-weight: 300;
    font-size: 20px;
    text-rendering: optimizeLegibility;
}

.row {
	max-width: 1140px;
	margin: 0 auto;
}

section {
	padding: 80px 0;
}

.app-container {
    padding-top: 10vh;
    text-align: center;
    background-image: url(background-1.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    height: 120vh;
}

.app-sheet {
    margin: 0 10vw 0 10vw;
    
}

.titles {
    text-transform: uppercase;
    font-size: 6vh;
    letter-spacing: 3px;
}

form {
}

.form-container {
    margin-top: 10vh;
    background-color: #547466;
    padding: 5vh 0 5vh 0;
    border-radius: 10px;
}

#city {
    margin-right: 20vw;
    width: 30vh;
    height: 6vh;
    transform: scale(1.3)
}

#country {
  
    width: 30vh;
    height: 6vh;
    transform: scale(1.3)
}
.weather {
    margin-top: 5vh;
    color: #fffefa;
    background-color: #547466;
    border-radius: 10px;
}

#get-weather {
    transform: scale(2);
    margin-top: 10vh;
    
}

#js--weather-data {

}

.weather-icon {
    width: 15vw;
    color: #fffefa;
    filter: invert(1);
}

.weather-block {
    text-align: justify;
    margin-top: 15%;
}

